import React, { Component } from 'react'
import './App.css'
import ThreeModel from './three/ThreeModel'
import models from './three/models'

const btnBack  = '/static/3d/back.png'
const btnIntro = '/static/3d/intro.png'
const playBtn  = '/static/3d/play.gif'
const pauseBtn = '/static/3d/pause.png'
const ex0 = '/static/3d/ex0.png'
const ex1 = '/static/3d/ex1.png'
const ex2 = '/static/3d/ex2.png'
const ex3 = '/static/3d/ex3.png'
const ex4 = '/static/3d/ex4.png'
const exs0 ='/static/3d/exs0.png'
const exs1 = '/static/3d/exs1.png'
const exs2 = '/static/3d/exs2.png'
const exs3 = '/static/3d/exs3.png'
const exs4 = '/static/3d/exs4.png'

class App extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      curEx : 0,
      title : '',
      intro : '',
      playing : false,
      curProgress: 0,
      mdl: null
    };
  }

  clickPlay = () => {
    this.audio.pause()
    switch (this.state.curEx) {
      case 0: 
        this.audio.src = 'http://cdn.atmuseum.cn/changzhou/3d/music0.mp3'
        break
      case 1: 
        this.audio.src = 'http://cdn.atmuseum.cn/changzhou/3d/music1.mp3'
      break
      case 2: 
        this.audio.src = 'http://cdn.atmuseum.cn/changzhou/3d/music2.mp3'
        break
      case 3: 
        this.audio.src = 'http://cdn.atmuseum.cn/changzhou/3d/music3.mp3'
        break
      default : 
        this.audio.src = 'http://cdn.atmuseum.cn/changzhou/3d/music4.mp3'
        break
    }
    if (!this.state.playing) {
      this.audio.play()
    }
    this.setState({
      playing : !this.state.playing
    });
  }

  clickEx = (i) => {
    this.audio.pause()
    this.setState({
      curEx : i
    });
    if (this.state.playing) {
      this.setState({
        playing : false
      });
    }
  }

  showModal = () => {
    switch (this.state.curEx) {
      case 0: 
        this.setState({
          title: '新石器时代良渚文化（公元前3300～前2200年）',
          intro: '乳白色，有青灰色斑块。矮方柱体，内圆外方，圆孔由两头对钻而成，孔壁光洁。四角大于90度，分为上下两节，各琢刻上下不同的纹饰。上节为戴羽冠的人面；下节象征兽面。以繁密的卷云纹作地。构图复杂，雕刻精细，为玉琮中的一件精品。'
        })
        this.modal.style.background = '#CAC3B7'
        break
      case 1: 
        this.setState({
          title: '春秋（公元前770～前475年）',
          intro: '侈口，束颈，浅腹圆鼓，圈足。胎体厚重坚致，内外施青黄色釉，器底无釉。肩部堆贴不等距分布的五只小鸟和一对绹纹耳，耳两旁附加S形堆纹；腹部用剔刺法制成细密的锥刺纹；腹面两侧各堆贴一道扉棱，施釉均匀，造型规整。'
        })
        this.modal.style.background = '#BDB4A3'
        break
      case 2: 
        this.setState({
          title: '五代－北宋（公元907～1127年）',
          intro: '釉呈青黄色。全器以盖与座两部分相合成扁圆球形，以子母口相扣，大圈足，足底微凸，足根外撇。盒盖镂刻卷草纹，茎叶之外皆镂空，使实用与装饰浑然一体。盖边及腹上部饰划花水波纹，腹下部刻划双层莲瓣纹。除盒盖的子口缘外，内外均施釉，底部见支烧痕。香熏造型精巧，釉色纯净，是越窑青瓷的佳作。'
        })
        this.modal.style.background = '#AFAFA2'
        break
      case 3: 
        this.setState({
          title: '南宋（公元1127～1279年）',
          intro: '观音头戴化佛冠，面相端庄安祥，弯眉细目，秀鼻小口，胸佩璎珞，身披通肩大衣，双手结定印，手腕戴环钏，跣足，善跏趺坐于石崖之上。前面为莲花插座，净瓶和小鸟分列左右。观音所披大衣和石崖处施青白釉，釉质滋润，有冰裂纹，其余部分都为涩胎。整件作品胎质坚致细腻，釉色均匀滋润，人物造型比例协调，线条流畅，体态轻盈，是我国古代瓷塑作品中十分罕见的艺术精品。'
        })
        this.modal.style.background = '#A9AEA7'
        break
      default : 
        this.setState({
          title: '南宋（1127年～1279年）',
          intro: '木胎，六出莲瓣筒形。分盖、盘、中、底四层，浅圈足，合口处镶银扣。外髹朱漆，内髹黑漆。盖面为戗金仕女消夏图，描绘二主一仆在石径上漫步，二主人着襦裙背子，梳高髻，着花罗直领对襟衫，长裙曳地；一持折扇，一抱团扇挽臂喁喁私语；侍女手捧玉壶春瓶恭立一旁；左侧放一绣墩，四周以假山、草木、花卉加以点缀，突出了时令气息。器壁以同样工艺饰牡丹、莲花、梅花、芙蓉等六组折枝花。盒内朱书“温州新河金念五郎上牢”十字。这件漆奁被国家文物鉴定委员会定为国宝级文物。'
        })
        this.modal.style.background = '#C2B7B6'
        break
    }
    this.modal.style.display = 'block'
  }

  hiddenModal = () => {
    this.modal.style.display = 'none'
  }

  getModelConfig = (id) => {
    var model = models[id] || {}
    model.path = `http://cdn.atmuseum.cn/changzhou/3d/${id}/`
    model.file = model.model || 'model.obj'
    model.mtl = model.mtl || 'model.mtl'
    return model
  }

  getModel = (id) => {
    let mdlData = this.getModelConfig(id)
    let options = {
        debug: false, // 是否开启调试
        elm: 'model', // 渲染画布存放的父节点ID
        scene: {
            transparent: true
        },
        ctrl: {
            type: 'orbit',
            options: {
                autoRotate: false,
                maxPolarAngle: (mdlData.options && mdlData.options.ctrl && mdlData.options.ctrl.maxPolarAngle) || Math.PI
            }
        },
        objects: {
            camera: {
                position: '0,1.6,1.6',
                fov: 45,  // 45度视角
            },
            lights: [
                {
                    type: 'hemisphere',
                    options: {
                        skyColor: 0xffffff,
                        groundColor: 0xffffff,
                        intensity: 1.0
                    }
                },
                {
                    type: 'point',
                    options: {
                        color: 0xffffff,
                        intensity: 0.1,
                        distance: 50,
                        position: '-2,1.75,1'
                    }
                }
            ],
            models: mdlData.objects || [mdlData]
        }
    }
    let mdl = new ThreeModel(options)
    this.setState({ mdl: mdl }, ()=>{
      this.state.mdl.show3DModel(options)
      this._getProgress(this.state.mdl)
    })
  }

  _getProgress = (obj) => {
    let curPro = obj.getProgress()
    if (curPro >= 100 || curPro < 0) {
      this.setState({
        curProgress: 100
      });
      return true
    } else {
      this.setState({
        curProgress: curPro
      });
      setTimeout(() => this._getProgress(obj), 100)
    }
  }

  componentDidMount(){
    this.getModel(this.state.curEx)
  }
  
  componentWillUpdate(nextProps, nextState){
    if (this.state.curEx !== nextState.curEx) {
      let mdlData = this.getModelConfig(nextState.curEx)
      let options = {
          debug: false, // 是否开启调试
          elm: 'model', // 渲染画布存放的父节点ID
          scene: {
              transparent: true
          },
          ctrl: {
              type: 'orbit',
              options: {
                  autoRotate: false,
                  maxPolarAngle: (mdlData.options && mdlData.options.ctrl && mdlData.options.ctrl.maxPolarAngle) || Math.PI
              }
          },
          objects: {
              camera: {
                  position: '0,0.8,2',
                  fov: 45,  // 45度视角
              },
              lights: [
                  {
                      type: 'hemisphere',
                      options: {
                          skyColor: 0xffffff,
                          groundColor: 0xffffff,
                          intensity: 1.0
                      }
                  },
                  {
                      type: 'point',
                      options: {
                          color: 0xffffff,
                          intensity: 0.1,
                          distance: 50,
                          position: '-2,1.75,1'
                      }
                  }
              ],
              models: mdlData.objects || [mdlData]
          }
      }
      this.state.mdl.show3DModel(options)
      this._getProgress(this.state.mdl)
    }
  }
  render() { 
    let {curEx, playing, title, intro, curProgress} = this.state

    return (
      <div className="App" style={{background: `url(/static/3d/bg${curEx}.jpg) center no-repeat / cover`}}>
        <img src={btnIntro} className="img-btn" alt="正在加载中" onClick={this.showModal}/>
        <img src={playing ? playBtn : pauseBtn} className="play-btn" alt="正在加载中" onClick={this.clickPlay}/>
        <img src={`static/3d/title${curEx}.png`} className="img-title" alt="正在加载中"/>

        <div id="model" className="model-content" />

        <div className="navbar" >
          <img src={(curEx===0) ? ex0 : exs0} className="icon-btn" alt="正在加载中" onClick={() => this.clickEx(0)}/>
          <img src={(curEx===1) ? ex1 : exs1} className="icon-btn" alt="正在加载中" onClick={() => this.clickEx(1)}/>
          <img src={(curEx===2) ? ex2 : exs2} className="icon-btn" alt="正在加载中" onClick={() => this.clickEx(2)}/>
          <img src={(curEx===3) ? ex3 : exs3} className="icon-btn" alt="正在加载中" onClick={() => this.clickEx(3)}/>
          <img src={(curEx===4) ? ex4 : exs4} className="icon-btn" alt="正在加载中" onClick={() => this.clickEx(4)}/>
        </div>

        <div className="App-footer" />

        <div className="modal-container" style={{ display: 'none' }} ref={(ref) => { this.modal = ref; }}>
          <div className="modal-content" style={{ height: 'calc(85vh - 14vw)' }}>
            <img src={btnBack} className="img-btn" alt="正在加载中" onClick={this.hiddenModal}/>
            <p className="text-content" style={{marginTop: '12vh'}}>{title}</p>
            <p className="text-content" style={{marginTop: '1.2em'}}>{intro}</p>
          </div>
        </div>

        <div className="modal-loading" ref={(ref) => { this.loading = ref; }}
        style={{display: curProgress === 100 ? 'none' : 'flex', background: 'url(static/3d/loading.png) center /cover'}}>
          <p className="loading-content">{curProgress}%</p>
        </div>

        <audio src='' ref={(ref) => { this.audio = ref; }}/>
      </div>
    );
  }
}

export default App;



